export default {
	props:{
		item:Object,
		index:Number,
		userInfo:Object
	},
	data() {
		return {
			isguanzhu: false,
			infoNum:this.item.infoNum,
			topicActive:{
				uid:this.userInfo.id,
				tid:this.item.id,
				tuid: this.item.uid
				
			}
		}
	},
	onLoad() {
		this.isguanzhu=this.item.isguanzhu
		this.infoNum=this.item.infoNum
		this.topicActive.uid = this.userInfo.id
		this.topicActive.tid = this.item.id
		this.topicActive.tuid = this.item.uid

		
		
	},
	methods:{
		// 关注
		guanzhu(){
			this.isguanzhu=true;
			uni.showToast({
				title: '关注成功',
			});
		},
		
		// 顶踩
		async caozuo(type){
			if(!this.userInfo || !this.userInfo.id){
				uni.showToast({
					title:"你还未登录！或登录过有效期!",
					icon:'none'
				})
				return
			}
			
			switch (type){
				case "ding":
				if(this.infoNum.index==1){ 
				
				await this.$emit("likeOrTread",{
						...this.topicActive,
						tactive: 0
					})
					this.infoNum.likeNum--;
					this.infoNum.index=0;
					return 
					
				}
				await this.$emit("likeOrTread",{
						...this.topicActive,
						tactive: 1
					})
				this.infoNum.likeNum++;
				if(this.infoNum.index==2){
					this.infoNum.treadNum--;
				}
				this.infoNum.index=1;
					break;
				case "cai":
				
				if(this.infoNum.index==2){
					await this.$emit("likeOrTread",{
							...this.topicActive,
							tactive: 0
						})
					// this.$http.post('/topic/active',{
					// 	tid:this.item.id,
					// 	uid:2,
					// 	tactive: 0
					// }).then((data)=>{
					this.infoNum.treadNum--;
					this.infoNum.index=0;
					// })
					
					return  
				}
				await this.$emit("likeOrTread",{
						...this.topicActive,
						tactive: 2
					})
				// this.$http.post('/topic/active',{
				// 	tid:this.item.id,
				// 	uid:2,
				// 	tactive: 2
				// }).then((data)=>{
					this.infoNum.treadNum++;
					if(this.infoNum.index==1){
						this.infoNum.likeNum--;
					}
					this.infoNum.index=2;
				// })
					break;
			}
		},
		// 进入详情页
		opendetail(){
			uni.setStorageSync("topicDatail",JSON.stringify(this.item))
			uni.navigateTo({
				url: '../../pages/detail/detail',
			});
		}
	}
}
